'use strict';

(function ($) {
    $(function () {
        WW.googlemapsFooter = {
            init: function init() {
                //var jsonLocation = $('#googlemap-1').data('json-location'),
                var jsonLocation = $(document.body).data('settings'),
                    MY_MAPTYPE_ID = 'custom_style';
                var map = undefined;
                $.getJSON(jsonLocation).done(function (data) {
                    initialize(data.app.map);
                }).fail(function () {
                    console.error('Failed to load ' + jsonLocation + '\n                            Possible error in JSON syntax.');
                });

                function initialize(mapConfig) {

                    var latLang = new google.maps.LatLng(mapConfig.mapParams.longitude, mapConfig.mapParams.latitude),
                        MY_MAPTYPE_ID = 'custom_style';
                    var featureOpts = undefined,
                        mapOptions = undefined,
                        styledMapOptions = undefined,
                        customMapType = undefined;

                    mapOptions = {
                        center: latLang,
                        zoom: mapConfig.mapParams.zoom,
                        draggable: true,
                        scrollwheel: false,
                        panControl: false,
                        mapTypeControl: false,
                        zoomControl: true,
                        streetViewControl: true,
                        mapTypeControlOptions: {
                            mapTypeIds: [google.maps.MapTypeId.ROADMAP, MY_MAPTYPE_ID]
                        },
                        mapTypeId: MY_MAPTYPE_ID
                    };

                    featureOpts = [
                        /* example feature option
                        {
                            featureType: 'road.highway',
                            elementType: "geometry",
                            stylers: [
                                { color: '#356751'},
                                {weight: 0.5}
                            ]
                        }
                        find more feature options http://gmaps-samples-v3.googlecode.com/svn/trunk/styledmaps/wizard/index.html
                        */
                    ];

                    styledMapOptions = {
                        name: 'Custom Style'
                    };

                    customMapType = new google.maps.StyledMapType(featureOpts, styledMapOptions);

                    map = new google.maps.Map(document.getElementById('googlemap-1'), mapOptions);
                    map.mapTypes.set(MY_MAPTYPE_ID, customMapType);

                    if (WW.isTouch) {
                        map.setOptions({
                            draggable: false
                        });
                    }

                    mapConfig.locations.forEach(createMarker);
                }

                function createMarker(location) {
                    var infowindow = new google.maps.InfoWindow(),
                        longitude = location.longitude,
                        latitude = location.latitude,
                        imageLocation = $('#googlemap-1').data('marker-url'),
                        image = undefined,
                        marker = undefined;

                    image = new google.maps.MarkerImage(imageLocation,
                    // This marker is 21 pixels wide by 30 pixels tall.
                    new google.maps.Size(21, 30),
                    // The origin for this image is 0,0.
                    new google.maps.Point(0, 0),
                    // The anchor for this image is the base of the flagpole at 18,42.
                    new google.maps.Point(10, 30));

                    marker = new google.maps.Marker({
                        map: map,
                        position: {
                            lat: longitude,
                            lng: latitude
                        },
                        visible: true,
                        icon: image // This path is the custom pin to be shown. Remove this line and the proceeding comma to use default pin
                    });

                    infowindow.setContent('\n                        <div class="info-window-hook">\n                            <div> \n                                ' + location.headline + ' \n                            </div>\n                            <div> \n                                ' + location.bodyText + ' \n                            </div>\n                            <div> \n                                ' + location.foo + ' \n                            </div>\n                            <a href="' + location.url + '">\n                                LÃ¤s mer &raquo;\n                            </a>\n                        </div>\n                    ');

                    google.maps.event.addListener(marker, 'click', function () {
                        infowindow.open(map, this);
                    });
                }
            }
        };
        WW.googlemapsFooter.init();
    });
})(jQuery);